import { LoadingProvider } from './../../providers/loading';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { NavParams, AlertController, ToastController, ModalController } from '@ionic/angular';
import { Device } from './../../models/';
import { BLEProvider, DataProvider, LocalDatabaseProvider } from './../../providers';
import {
  timer,
  Subscription,
} from 'rxjs';
import { take } from 'rxjs/operators';

interface AdvertisementData {
  runtime: number;
  temp: number;
  water: number;
  id: number;
  energy: number;
  cost: number;
  gasAmount: number;
}

interface BleDevice {
  advertising: ArrayBuffer | any;
  id: string;
  name: string;
  rssi: number;
  data: {
    runtime: number,
    temp: number,
    water: number,
    id: number
  };
}

interface Rating {
  emojiColor: '#42B771' | '#F4D41E' | '#E84652';
  emojiUrl: 'assets/icon/grin-regular.svg' | 'assets/icon/meh-regular.svg' | 'assets/icon/meh-regular.svg';
  progress: 75 | 50 | 25;
}

interface PreviousSessionData {
  runtime: number;
  temp: number;
  water: number;
  id: number;
  energy: number;
  cost: number;
  gasAmount: number;
  rating: Rating;
}

@Component({
  selector: 'app-add-device',
  templateUrl: './add-device.page.html',
  styleUrls: ['./add-device.page.scss'],
})
export class AddDevicePage implements OnInit, OnDestroy {
  device: Device;
  newDeviceSub: Subscription;
  timerSub: Subscription;
  constructor(
    public navParams: NavParams,
    public alertController: AlertController,
    public toastController: ToastController,
    public modalController: ModalController,
    public ble: BLEProvider,
    public data: DataProvider,
    public localDb: LocalDatabaseProvider,
    public loading: LoadingProvider
    ) {

    this.device = this.navParams.get('device');
    console.log('this.navParams.get(device)', this.device);

  }

  ngOnInit() {
    // if (!this.device) {
    //   this.newDeviceSub = this.ble.newDeviceChanged.subscribe(device => {
    //     console.log('newDeviceChanged', device);
    //     this.presentDeviceFoundConfirm(device.id);
    //   });
    // }
  }

  ngOnDestroy() {
    this.ble.ble.stopScan();
  }

  ionViewWillLeave() {
    console.log('ionViewWillLeave');

    this.ble.ble.stopScan();
    if (this.newDeviceSub) {
      this.newDeviceSub.unsubscribe();
    }
    if (this.timerSub) {
      this.timerSub.unsubscribe();
    }
    this.ble.resetAddDevices();
  }

  ionViewWillEnter() {
    console.log('ionViewWillEnter');

    if (this.data && this.data.user && this.data.user.devices && this.data.user.devices.length > 0) {
      const userDeviceIds = this.data.user.devices.map(item => item.id);
      this.ble.addIdsToDeviceIds(userDeviceIds);
    }

    if (!this.device) {
      this.newDeviceSub = this.ble.newDeviceChanged.subscribe((res) => {
        console.log('newDeviceChanged', res);
        this.presentDeviceFoundConfirm(res.advertisementData, res.bleDevice);
      });

      this.timerSub = timer(0, 5000).subscribe(value => { // start scanning to refresh view
        // console.log(value);

        this.ble.startAddDeviceScan();

      });

      // this.ble.connect(device.id).subscribe(async peripheral => {
      //   console.log('peripheral', peripheral);

      //   const cumulativeData = await this.ble.read(peripheral.id, this.showerDevice.serviceUUID, this.showerDevice.cumulative);



      // this.ble.startAddDeviceScan();
    }
     // TODO: move also presentDeviceFoundConfirm here? it's not always showing
  }

  async presentDeviceFoundConfirm(device: AdvertisementData, bleDevice: BleDevice) {
    const alert = await this.alertController.create({
      header: `Found device ${device.id}`,
      message: 'Do you want to add it to the devices to measure',
      backdropDismiss: false,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('cancel clicked');
          }
        }, {
          text: 'Add',
          handler: async () => {
            console.log('add clicked');
            if (!this.data.user.devices) {
              this.data.user.devices = [];
            }
            const newDevice: Device = {
              id: device.id,
              name: device.id.toString()
            };


            this.loading.presentLoading(null, 'Connecting to Streamsave...');
            this.ble.ble.connect(bleDevice.id).subscribe(async peripheral => {
              if (peripheral) {
                this.data.user.devices.push(newDevice);
                this.localDb.updateUser(this.data.user);
                const cumulativeData = await this.ble.ble.read(peripheral.id, this.ble.showerDevice.serviceUUID, this.ble.showerDevice.cumulative);
                const parsedCumulativeData = this.ble.parseBLEData(cumulativeData);
                // console.log('parsedCumulativeData', parsedCumulativeData);

                await this.localDb.initPreviousMeasurement(parsedCumulativeData, newDevice.id);
                await this.localDb.addDeviceClosedMeasurement(newDevice.id);
                const dc = await this.ble.ble.disconnect(peripheral.id);
                console.log('dc', dc);
                const rating: Rating = {
                  emojiColor: "#42B771",
                  emojiUrl: "assets/icon/grin-regular.svg",
                  progress: 25

                }
                const session: PreviousSessionData = {
                  runtime: 0,
                  temp: 0,
                  water: 0,
                  id: newDevice.id,
                  energy: 0,
                  cost: 0,
                  gasAmount: 0,
                  rating: rating,
                }
                // this.showPreviousSessions.set(id, true);
                this.ble.deviceWasPaused.set(newDevice.id, false);
                this.ble.previousSessions.set(newDevice.id, session);
                this.loading.dismissLoading();
                console.log(this.ble.previousSessions);
                console.log(this.ble.previousSessions.get(newDevice.id));


                this.presentDeviceAddedToast(newDevice.name);
              } else {
                console.warn('coulnt not connect to device');
                this.loading.dismissLoading();
              }
            },
            error => {
              console.warn('error');
              this.loading.dismissLoading();
              this.handleError('Device disconnected unexpectedly');
            });
          }
        }
      ]
    });
    await alert.present();
  }

  async presentDeviceAddedToast(deviceId: string) {
    const toast = await this.toastController.create({
      message: `${deviceId} added to devices.`,
      duration: 4000
    });
    toast.present();
  }

  async renameDevice() {
    const alert = await this.alertController.create({
      header: 'Rename device',
      inputs: [
        {
          name: 'name',
          type: 'text',
          label: 'test',
          value: this.device && this.device.name ? this.device.name : '',
          placeholder: 'Device name'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            console.log('Confirm Cancel');
          }
        }, {
          text: 'Ok',
          handler: (data) => {
            console.log('data.name', data.name);
            console.log('Confirm Ok');
            const deviceIndex = this.data.user.devices.findIndex(device => device.id === this.device.id);
            this.data.user.devices[deviceIndex].name = data.name;
            this.device.name = data.name;
            this.localDb.updateUser(this.data.user);
          }
        }
      ]
    });
    await alert.present();
  }

  async removeDevice() {
    const deviceName = this.device && this.device.name ? this.device.name : this.device.id;
    const alert = await this.alertController.create({
      header: 'Remove device',
      message: `Do you want to remove device ${deviceName}`,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('cancel clicked');
          }
        }, {
          text: 'Remove',
          handler: async () => {
            console.log('remove clicked');
            const deviceIndex = this.data.user.devices.findIndex(device => device.id === this.device.id);
            this.data.user.devices.splice(deviceIndex, 1);
            await this.localDb.updateUser(this.data.user);
            await this.localDb.clearMeasurementsByDeviceId(this.device.id, true);
            await this.localDb.deleteDeviceClosedMeasurement(this.device.id);
            this.back();
          }
        }
      ]
    });
    await alert.present();
  }

  back() {
    this.modalController.dismiss();
  }

  async handleError(errorMsg: string) {
    const alert = await this.alertController.create({
      header: 'Something went wrong',
      subHeader: errorMsg,
      message: 'If the problem persists, try restarting the app and switching bluetooth off and on again.',
      buttons: ['OK']
    });
    await alert.present();
  }
}
