
import {
    Entity,
    PrimaryGeneratedColumn,
    Column
  } from 'typeorm';


  @Entity('measurement')
  export class MeasurementEntity {

    @PrimaryGeneratedColumn()
    id: number;

    @Column()
    deviceId: number;

    @Column()
    runtime: number;

    @Column()
    water: number;

    @Column()
    energy: number;

    @Column()
    bootCount: number;

    @Column()
    startTs: number;

    @Column()
    endTs: number;

  }