import {
    Entity,
    Column,
    PrimaryColumn
  } from 'typeorm';


  @Entity('deviceClosedMeasurement')
  export class DeviceClosedMeasurementEntity {

    @PrimaryColumn()
    deviceId: number;

    @Column()
    runtime: number;

    @Column()
    water: number;

    @Column()
    energy: number;

    @Column()
    bootCount: number;

    @Column()
    startTs: number;

    @Column()
    endTs: number;
  }