
import {
  Entity,
  PrimaryGeneratedColumn,
  Column,
  JoinTable
} from 'typeorm';


@Entity('user')
export class UserEntity {

  @PrimaryGeneratedColumn()
  uid: number;

  @Column()
  email: string;

  @Column()
  allowNotifications: boolean;

  @Column()
  devices: string;

  @Column()
  ranges: string;

  @Column()
  units: string;

  /*     @JoinTable()
      devices: DeviceEntity[];

      @JoinTable()
      units: UnitsEntity; */

  // @Column()
  // ranges: Ranges;

  // @Column()
  // units: Units;

}