import {
  Measurement
} from './../models/measurement';
import {
  LocalDatabaseProvider
} from './localdb';
import {
  Injectable,
  NgZone
} from '@angular/core';
import {
  ToastController,
  AlertController,
  Platform
} from '@ionic/angular';
import {
  BLE
} from '@ionic-native/ble/ngx';
import {
  OpenNativeSettings
} from '@ionic-native/open-native-settings/ngx';
import {
  Subject,
  Subscription,
  BehaviorSubject
} from 'rxjs';
import {
  first,
  groupBy,
  mergeMap,
  throttleTime
} from 'rxjs/operators';


import {
  LoadingProvider
} from './loading';
import {
  DataProvider
} from './data';

import {
  getTime,
  endOfDay, startOfDay
} from 'date-fns';

import {
  LocalNotifications,
  ELocalNotificationTriggerUnit
} from '@ionic-native/local-notifications/ngx';
import {
  DeviceClosedMeasurementEntity
} from 'src/entities/deviceClosedMeasurement';


interface BleDevice {
  advertising: ArrayBuffer | any;
  id: string;
  name: string;
  rssi: number;
  data: {
    runtime: number,
    temp: number,
    water: number,
    id: number
  };
}

interface AdvertisementData {
  runtime: number;
  temp: number;
  water: number;
  id: number;
  energy: number;
  cost: number;
  gasAmount: number;
}

// bad #E84652
// medi #F4D41E
// good #42B771
interface Rating {
  emojiColor: '#42B771' | '#F4D41E' | '#E84652';
  emojiUrl: 'assets/icon/grin-regular.svg' | 'assets/icon/meh-regular.svg' | 'assets/icon/meh-regular.svg';
  progress: 75 | 50 | 25;
}

interface IsDeviceRunning {
  isRunning: boolean;
  lastTs: number;
  deviceId: number;
}

interface PreviousSessionData {
  runtime: number;
  temp: number;
  water: number;
  id: number;
  energy: number;
  cost: number;
  gasAmount: number;
  rating: Rating;
}

@Injectable()
export class BLEProvider {

  public devices: BleDevice[] = [];
  peripheral: any;
  public peripherals: any = [];
  statusMessage: string;
  connectionStatus: string;

  newDevice: AdvertisementData;
  // newDeviceSub: Subscription;
  newDeviceChanged = new Subject < {
    advertisementData: AdvertisementData,
    bleDevice: BleDevice
  } > ();

  deviceIsRunning: IsDeviceRunning[] = [];
  deviceIsRunningChanged = new BehaviorSubject < IsDeviceRunning > (null);

  scanSub: Subscription;

  oldDeviceLength: number = 0;

  // bleAdvertisement = ['company id, 2B']['firmware version, 2B']['"SHOWER" magic string to id shower sensor, 6B']
  // ['Runtime shower runtime in seconds, 2B']['Temperature water temp where 1 unit is 0.1C, 2B']
  // ['Water how many littres in this session, 2B']['Shower id shower id number, 1B'];

  // characteristicContent = ['runtime, 4B']['water 4B']['energy 8B']['boot count, 2B'];


  showerDevice = {
    serviceUUID: '00001523-1212-efde-1523-785feabcd123',
    cumulative: '00001526-1212-efde-1523-785feabcd123',
    conifg: '00001524-1212-efde-1523-785feabcd123',
    reset: ' 0x FF FF FF FF'
  };

  deviceIds: any[] = [];

  ratings = new Map<number, Rating> ();
  ratingChanged = new BehaviorSubject < Map<number, Rating> > (
    null
  );

  cumulativeDataReadChanged = new BehaviorSubject < number > (null);

  deviceWasPaused = new Map<number, boolean> ();

  previousSessions = new Map<number, PreviousSessionData> ();

  constructor(
    public toastCtrl: ToastController,
    public alertCtrl: AlertController,
    public ble: BLE,
    public openNativeSettings: OpenNativeSettings,
    public ngZone: NgZone,
    public loading: LoadingProvider,
    public platform: Platform,
    public data: DataProvider,
    public localDb: LocalDatabaseProvider,
    public localNotifications: LocalNotifications
  ) {}


  startAddDeviceScan() {
    console.log('startAddDeviceScan');
    this.ngZone.runOutsideAngular(() => {
      this.ble.startScanWithOptions([], {
        reportDuplicates: false
      }).subscribe(
        (device: BleDevice) => {
          // console.log(device);

          this.addDeviceToUsersDevices(device);
        },
        error => {
          console.log('error', error);
        }
      );
      setTimeout(() => {
        this.ble.stopScan().then(() => {
          // console.log('scan stopped');
        });
      }, 4500);
    });
  }

  addDeviceToUsersDevices(device: BleDevice) {
    this.ngZone.runOutsideAngular(() => {
      if (device.name === 'SHOWER' && device.id && device.rssi) {
        console.log('device', device);

        let humanReadlableData: AdvertisementData;

        if (this.platform.is('android')) {
          const parsed = this.parseAdvertisingData(device.advertising);
          console.log('parsed', parsed);
          const data = parsed['0xff'];
          humanReadlableData = this.onReceivedDataChange(data);
          console.log('humanReadlableData', humanReadlableData);
          if (!humanReadlableData.id) {
            return;
          }
        } else if (this.platform.is('ios')) {
          const mfgData = new Uint8Array(device.advertising.kCBAdvDataManufacturerData);
          console.log('Manufacturer Data is', mfgData);
          const data = mfgData.buffer;
          humanReadlableData = this.onReceivedDataChange(data);
          if (!humanReadlableData.id) {
            return;
          }
        }
        device.data = humanReadlableData;

        if (this.deviceIds.map(item => item.id).indexOf(humanReadlableData.id) < 0) {
          this.deviceIds.push(humanReadlableData);
          // advertisementData: AdvertisementData, bleDevice: BleDevice
          this.newDeviceChanged.next({
            advertisementData: {
              ...humanReadlableData
            },
            bleDevice: {
              ...device
            }
          });

        } else {
          console.warn('device is allreayd in the list');
        }
      }
    });
  }

  resetAddDevices() {
    this.deviceIds = [];
  }

  addIdsToDeviceIds(ids: number[]) {
    for (const id of ids) {
      if (this.deviceIds.indexOf(id) < 0) {
        const advData: AdvertisementData = {
          cost: 0,
          energy: 0,
          gasAmount: 0,
          id: id,
          runtime: 0,
          temp: 0,
          water: 0
        };
        this.deviceIds.push(advData);
      }
    }
  }

  /**
   * resetCumulativeData
   * @param deviceId
   * @param serviceUUID
   * @param characteristic
   */
  async resetCumulativeData(deviceId: string): Promise < any > {
    try {
      const resetPayload = new Uint8Array([
        255, 255, 255, 255
      ]);
      const writeResponse = await this.ble.write(
        deviceId,
        this.showerDevice.serviceUUID,
        this.showerDevice.cumulative, < ArrayBuffer > resetPayload.buffer);
      return writeResponse;
    } catch (error) {
      throw error;
    }
  }

  /**
   * startRefreshScan
   */
  startRefreshScan() {
    // console.log('startRefreshScan');

    this.ngZone.runOutsideAngular(() => {
      if (this.platform.is('cordova')) {
        // console.log('startRefreshScan 2' );

        const selector = (x) => x.id;
        const throttleTimeout = 1000;
        this.ble.startScanWithOptions([], {
            reportDuplicates: true
          })
          .pipe(
            groupBy(selector),
            mergeMap((group$) => group$.pipe(throttleTime(throttleTimeout)))
          )
          .subscribe(
            (device: BleDevice) => {
              // console.log('device', device);

              this.onDeviceDiscovered(device);
            },
            error => {
              console.log('error', error);
            }
          );
        // const self = this;
        setTimeout(() => {
          this.ble.stopScan().then(() => {
            // console.log('scan stopped');
          });
        }, 9000);
      }
    });
  }

  /**
   * Scan BLE devices
   */
  scan() {
    this.ngZone.run(() => {
      console.log('scan()');
      this.ble.isEnabled().then(() => {
        this.ble.scan([], 1).subscribe(
          device => this.onDeviceDiscovered(device),
          error => this.scanError(error)
        );
      }).catch(async () => {
        console.log('BLE not enabled');
      });
      this.ble.stopScan()
    });
  }

  /**
   * Device discovered
   * @param device
   */
  onDeviceDiscovered(device) {
    this.ngZone.runOutsideAngular(() => {

      if (device.name === 'SHOWER' && device.id && device.rssi) {
        let humanReadlableData;
        if (this.platform.is('android') && device.advertising) {
          const parsed = this.parseAdvertisingData(device.advertising);
          const data = parsed['0xff'];
          humanReadlableData = this.onReceivedDataChange(data);
        } else if (this.platform.is('ios') && device.advertising && device.advertising.kCBAdvDataManufacturerData) {
          const mfgData = new Uint8Array(device.advertising.kCBAdvDataManufacturerData);
          const data = mfgData.buffer;
          humanReadlableData = this.onReceivedDataChange(data);
        }

        if (this.data.user.devices && this.data.user.devices.length > 0) {
          const deviceIndex = this.data.user.devices.findIndex(item => item.id === humanReadlableData.id);
          if (deviceIndex >= 0) {
            this.ngZone.run(() => {
              if (this.deviceWasPaused.get(humanReadlableData.id)) {

                const previous: PreviousSessionData = this.previousSessions.get(humanReadlableData.id);

                if (previous && humanReadlableData && humanReadlableData.runtime >= previous.runtime) {
                  this.data.user.devices[deviceIndex].advertisementData.cost = humanReadlableData.cost;
                  this.data.user.devices[deviceIndex].advertisementData.energy = humanReadlableData.energy;
                  this.data.user.devices[deviceIndex].advertisementData.gasAmount = humanReadlableData.gasAmount;
                  this.data.user.devices[deviceIndex].advertisementData.runtime =  humanReadlableData.runtime;
                  this.data.user.devices[deviceIndex].advertisementData.temp = humanReadlableData.temp;
                  this.data.user.devices[deviceIndex].advertisementData.water =  humanReadlableData.water;

                } else {
                  this.data.user.devices[deviceIndex].advertisementData.cost = this.previousSessions.get(humanReadlableData.id).cost + humanReadlableData.cost;
                  this.data.user.devices[deviceIndex].advertisementData.energy = this.previousSessions.get(humanReadlableData.id).energy + humanReadlableData.energy;
                  this.data.user.devices[deviceIndex].advertisementData.gasAmount = this.previousSessions.get(humanReadlableData.id).gasAmount + humanReadlableData.gasAmount;
                  this.data.user.devices[deviceIndex].advertisementData.runtime = this.previousSessions.get(humanReadlableData.id).runtime + humanReadlableData.runtime;
                  this.data.user.devices[deviceIndex].advertisementData.temp = humanReadlableData.temp;
                  this.data.user.devices[deviceIndex].advertisementData.water = this.previousSessions.get(humanReadlableData.id).water + humanReadlableData.water;
                }
                const newRating = this.getRating(this.data.user.devices[deviceIndex].advertisementData.cost);
                this.ratings.set(this.data.user.devices[deviceIndex].advertisementData.id, newRating);
                this.ratingChanged.next(this.ratings);
              } else {
                this.data.user.devices[deviceIndex].advertisementData = {
                  ...humanReadlableData
                };
                const newRating = this.getRating(this.data.user.devices[deviceIndex].advertisementData.cost);
                this.ratings.set(this.data.user.devices[deviceIndex].advertisementData.id, newRating);
                this.ratingChanged.next(this.ratings);

              }
              if (!this.deviceIsRunning.some(item => item.deviceId === humanReadlableData.id)) {
                this.deviceIsRunning.push({
                  isRunning: true,
                  lastTs: Date.now(),
                  deviceId: humanReadlableData.id
                });
              } else {
                const deviceRunningIndex = this.deviceIsRunning.findIndex(item => item.deviceId === humanReadlableData.id);
                if (deviceRunningIndex < 0) {
                  return;
                }
                this.deviceIsRunning[deviceRunningIndex].deviceId = humanReadlableData.id;
                this.deviceIsRunning[deviceRunningIndex].lastTs = Date.now();
                this.deviceIsRunning[deviceRunningIndex].isRunning = true;

                this.deviceIsRunningChanged.next({
                  isRunning: true,
                  lastTs: Date.now(),
                  deviceId: humanReadlableData.id
                });
              }
            });

            if (!this.devices.some(item => item.id === device.id)) {

              this.devices.push(device);
              this.ble.connect(device.id).subscribe(async peripheral => {
                if (peripheral) {
                  const cumulativeData = await this.ble.read(peripheral.id, this.showerDevice.serviceUUID, this.showerDevice.cumulative);
                  const previousCumulativeData = await this.localDb.getPreviousMeasurement(humanReadlableData.id);
                  const parsedCumulativeData = this.parseBLEData(cumulativeData);

                  if (this.data.user.allowNotifications) {
                    const msg = 'Shower ' + this.data.user.devices[deviceIndex].name + ' is running';
                    this.localNotifications.schedule({
                      id: this.data.user.devices[deviceIndex].id,
                      title: 'Streamsave',
                      text: msg,
                      sound: null,
                      vibrate: true,
                      foreground: true,
                      trigger: {
                        in: 5,
                        unit: ELocalNotificationTriggerUnit.SECOND
                      },
                      icon: 'res://icon.png',
                      smallIcon: 'res://icon_small',
                    });
                  }

                    if (previousCumulativeData.runtime > parsedCumulativeData.runtime || previousCumulativeData.water > parsedCumulativeData.water || previousCumulativeData.energy > parsedCumulativeData.energy || previousCumulativeData.bootCount > parsedCumulativeData.bootCount) {
                      console.error('previousCumulativeData > parsedCumulativeData');
                    }


                  if (parsedCumulativeData.runtime && parsedCumulativeData.water && parsedCumulativeData.energy && parsedCumulativeData.bootCount) {
                    this.data.user.devices[deviceIndex].cumulativeData = {
                      ...parsedCumulativeData
                    };
                    // const resetOk = await this.resetCumulativeData(peripheral.id);
                    // console.log('resetOk', resetOk);
                    const dateNow = getTime(endOfDay(Date.now()));
                    const previousMeasurement: Measurement = {
                      runtime: parsedCumulativeData.runtime,
                      water: parsedCumulativeData.water,
                      energy: parsedCumulativeData.energy,
                      bootCount: parsedCumulativeData.bootCount,
                      deviceId: this.data.user.devices[deviceIndex].id,
                      startTs: this.data.previousTs,
                      endTs: dateNow,
                      id: null
                    };
                    const measurement: Measurement = {
                      runtime: parsedCumulativeData.runtime - previousCumulativeData.runtime,
                      water: parsedCumulativeData.water - previousCumulativeData.water,
                      energy: parsedCumulativeData.energy - previousCumulativeData.energy,
                      bootCount: parsedCumulativeData.bootCount - previousCumulativeData.bootCount,
                      deviceId: this.data.user.devices[deviceIndex].id,
                      startTs: this.data.previousTs,
                      endTs: dateNow,
                      id: null
                    };

                    if (measurement.runtime > 0 && measurement.water > 0 && measurement.energy > 0 && measurement.bootCount > 0) {

                      const currentDay = await this.localDb.getCurrentDayMeasurement(measurement.deviceId, measurement.endTs);
                      if (currentDay && currentDay.length > 0) {
                        await this.localDb.updateMeasurement(measurement);
                        await this.localDb.updatePreviousMeasurement(previousMeasurement);
                        this.data.setPreviousTs(dateNow);
                        this.cumulativeDataReadChanged.next(measurement.deviceId);
                      } else {
                        await this.localDb.addMeasurement(measurement);
                        await this.localDb.updatePreviousMeasurement(previousMeasurement);
                        this.data.setPreviousTs(dateNow);
                        this.cumulativeDataReadChanged.next(measurement.deviceId);
                      }

                      const startTs = getTime(startOfDay(Date.now()));
                      const endTs = getTime(endOfDay(Date.now()));

                      const measurementDeviceClosed: DeviceClosedMeasurementEntity = {
                        runtime: 0,
                        water: 0,
                        energy: 0,
                        bootCount: 0,
                        deviceId: device.id,
                        startTs: startTs,
                        endTs: endTs,
                      };
                      await this.localDb.updateDeviceClosedMeasurement(measurementDeviceClosed);
                    } else {
                      console.error('Measurement negative values');
                    }
                  }
                  const dc = await this.ble.disconnect(device.id);
                  console.log('dc', dc);
                }
                },
                error => {
                  this.handleError('Device disconnected unexpectedly');
                  console.log('error', error);
                });
            }
          }
        }
      }
    });
  }

  /**
   * Set status message
   * @param message
   */
  setStatus(message) {
    this.ngZone.run(() => {
      this.statusMessage = message;
    });
  }

  /**
   * If location permission is denied, you'll end up here
   */
  scanError(error) {
    this.setStatus('Error ' + error);
    this.showToast('Error scanning for Bluetooth low energy devices');
  }

  /**
   * Device selection
   * @param device
   */
  deviceSelected(device) {

    this.loading.presentLoading(5000, 'Connecting to device');

    this.ble.connect(device.id).subscribe(
      peripheral => {
        this.onConnected(peripheral);
        this.loading.dismissLoading();
      },
      peripheral => this.onDeviceDisconnected(peripheral)
    );
  }

  /**
   * Peripheral connected
   */
  onConnected(peripheral) {
    this.peripheral = peripheral;
    this.setStatus('Connected to ' + (peripheral.name || peripheral.id));
    console.log('this.peripheral', this.peripheral);
    this.getConnectStatus();
    // this.devices = [];
  }

  /**
   * Disconnect peripheral
   */
  disconnect() {
    this.ble.disconnect(this.peripheral.id)
      .then((data) => {
        console.log('good', data);
        this.peripheral = null;
        this.devices = [];
        this.ble.stopScan();
        // this.testF();

        // good disconnect
        this.getConnectStatus();
      })
      .catch(error => {
        console.error(error);
        // bad disconnect
      });
  }

  /**
   * When device is disconnected shot this toast
   */
  onDeviceDisconnected(peripheral) {
    this.showToast('The peripheral unexpectedly disconnected');
    this.getConnectStatus();
  }

  /**
   * returns value if bluetooth is on or off or is devices is connected or not
   */
  getConnectStatus() {
    this.ble.isEnabled().then(
      () => {
        if (this.peripheral && this.peripheral.id) {
          (this.ble.isConnected(this.peripheral.id)).then(
            () => {
              this.connectionStatus = 'connected';
            },
            () => {
              this.connectionStatus = 'notConnected';
            }
          );
        } else {
          this.connectionStatus = 'notConnected';
        }

      },
      () => {
        this.connectionStatus = 'noBT';
      }
    );
  }

  parseAdvertisingData(buffer) {
    var length, type, data, i = 0,
      advertisementData = {};
    var bytes = new Uint8Array(buffer);

    while (length !== 0) {

      length = bytes[i] & 0xFF;
      i++;

      // decode type constants from https://www.bluetooth.org/en-us/specification/assigned-numbers/generic-access-profile
      type = bytes[i] & 0xFF;
      i++;

      data = bytes.slice(i, i + length - 1).buffer; // length includes type byte, but not length byte
      i += length - 2; // move to end of data
      i++;

      advertisementData[this.asHexString(type)] = data;
    }

    // console.log('advertisementData', advertisementData);

    return advertisementData;
  }
  // i must be < 256
  asHexString(i) {
    var hex;

    hex = i.toString(16);

    // zero padding
    if (hex.length === 1) {
      hex = "0" + hex;
    }

    return "0x" + hex;
  }

  /**
   * Received data
   */
  parseBLEData(buffer: ArrayBuffer) {
    const data = new Uint8Array(buffer);
    // console.log('data', data);

    const dataView1 = new DataView(data.buffer);
    // console.log('dataview', dataView1);

    const value0 = dataView1.getUint32(0, true);
    const value1 = dataView1.getUint32(4, true);
    // const dataView2 = new DataView(buffer.buffer);
    const value2 = dataView1.getFloat64(8, true);
    // const dataView3 = new DataView(buffer.buffer);
    const value3 = dataView1.getUint16(16, true);

    // console.log('runtime', value0, 'water', value1, 'energy:', value2, 'bootcount:', value3);

    return {
      runtime: value0,
      water: value1,
      energy: value2,
      bootCount: value3
    };

  }

  /**
   * Received data
   */
  onReceivedDataChange(buffer: ArrayBufferLike): AdvertisementData {
    const data = new Uint8Array(buffer);
    // console.log('data', data);

    let debugBytes = '';
    for (let i = 0; i < data.byteLength; i++) {
      // debugBytes = debugBytes.concat(data[i].toString(16));
      // debugBytes = debugBytes.concat(",");
    }

    const dataView1 = new DataView(data.buffer);
    const value0 = dataView1.getUint16(9, false);
    const value1 = dataView1.getUint16(11, false);
    // const dataView2 = new DataView(buffer.buffer);
    const value2 = dataView1.getUint16(13, false);
    // const dataView3 = new DataView(buffer.buffer);
    const value3 = dataView1.getUint8(15);

    const t0 = 4; // cold water before heating
    const t = value1 * 0.1;

    const waterHeatCapacity = 4.1813; // J/gK
    let energy = value2 * (t - t0) * waterHeatCapacity / (3.6 * 1000);
    if (t < t0) {
      energy = 0;
    }

    const waterCost = value2 / 1000 * this.data.user.units.waterPrice; // water price /cubic metre

    let energyCost; //  / kWh * energy amount
    const btu2kw = 0.2931 / 1000;
    const gasFt3 = energy / btu2kw / this.data.user.units.gas.heatContent;

    if (this.data.user.units.heatingMethod === 'electricity') {
      energyCost = energy * (this.data.user.units.electricity.kWhPrice / 100);
    }
    if (this.data.user.units.heatingMethod === 'gas') {
      energyCost = this.getGasCost(gasFt3);
    }
    const cost = waterCost + energyCost;

    // Kaasun mr (ft3):
    // btu2kw = 0.2931/1000
    // kaasuFt3 = energiakWh / btu2kw / gasHeatContent

    return {
      runtime: value0,
      temp: value1 * 0.1,
      water: value2,
      id: value3,
      energy: energy,
      cost: cost,
      gasAmount: gasFt3
    };
  }

  getGasCost(gasAmountFt3): number {
    return (gasAmountFt3 / 100) * (this.data.user.units.gas.gasPrice / 100);
  }

  getRating(consumption: number): Rating {
    let emojiUrl;
    let emojiColor;
    let progress;
    if (consumption <= this.data.user.ranges.good) {
      emojiUrl = 'assets/icon/grin-regular.svg';
      emojiColor = '#42B771';
      progress = 25;
    } else if (consumption > this.data.user.ranges.good && consumption <= this.data.user.ranges.mediocre) {
      emojiUrl = 'assets/icon/meh-regular.svg';
      emojiColor = '#F4D41E';
      progress = 50;
    } else if (consumption >= this.data.user.ranges.needsImprovement) {
      emojiUrl = 'assets/icon/meh-regular.svg';
      emojiColor = '#E84652';
      progress = 75;
    }
    return {
      emojiColor: emojiColor,
      emojiUrl: emojiUrl,
      progress: progress
    };
  }

  /**
   * Show toast
   */
  async showToast(message) {
    const toast = await this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'middle'
    });
    await toast.present();
  }

  async handleError(errorMsg: string) {
    const alert = await this.alertCtrl.create({
      header: 'Something went wrong',
      subHeader: errorMsg,
      message: 'If the problem persists, try restarting the app and switching bluetooth off and on again.',
      buttons: ['OK']
    });
    await alert.present();
  }
}