import {
  Gas
} from './gas';
import {
  Electricity
} from './electricity';

export class Units {
  constructor(
    public water: 'litres' | 'gallons', // always save all measurement data in litres, parse gallons client side
    public temperature: 'c' | 'f', // always save all measurement data in C, parse f client side
    public currency: 'eur' | 'usd',
    public waterPrice: number, // always save as eur/m2, parse in other clientside
    public heatingMethod: 'electricity' | 'gas',
    public electricity: Electricity,
    public gas: Gas
  ) {}
}
