import {
    Entity,
    Column,
    PrimaryColumn
  } from 'typeorm';


  @Entity('previousMeasurement')
  export class PreviousMeasurementEntity {

    @PrimaryColumn()
    deviceId: number;

    @Column()
    runtime: number;

    @Column()
    water: number;

    @Column()
    energy: number;

    @Column()
    bootCount: number;
  }
