import { Injectable, NgZone } from '@angular/core';
// import { AuthProvider } from './auth';
import { Subscription, BehaviorSubject, Observable } from 'rxjs';

import { NativeStorage } from '@ionic-native/native-storage/ngx';
import { Platform } from '@ionic/angular';
import { Measurement, User } from '../models';

@Injectable()
export class DataProvider {
    user: User = new User(null, null, null, null, null, null);
    userSub: Subscription;
    userChanged = new BehaviorSubject < User > (new User(null, null, null, null, null, null));

    previousTs: number;

    previousFromToSelection: {from: number, to: number};

    previousCumulativeMeasurement: Measurement;

    chartTypeSelection: {id: number, name: string};
    // All devices id: -99
    deviceSelected: {
      name: string,
      id: number
    };

    selectedDateRange: {from: number, to: number};

  constructor(
      // private auth: AuthProvider
      private nativeStorage: NativeStorage,
      private platform: Platform,
      public ngZone: NgZone
  ) {
    this.platform.ready().then(() => {
      if (this.platform.is('cordova')) {
        this.initPreviousTs();
        // this.initPreviousFromToSelection();
      }
    });
  }

  setPreviousTs(ts) {
    this.nativeStorage.setItem('previousTs', {previousTs: ts});
  }

  setPreviousFromToSelection(from: number, to: number) {
    this.nativeStorage.setItem('previousFromToSelection', {from: from, to: to});
  }

  resetDeviceAdvertisementData(deviceId: number) {
    const deviceIndex = this.user.devices.findIndex(item => item.id === deviceId);
    if (deviceIndex < 0) {
      console.warn('no device found in user devices');
    } else {
      this.ngZone.run(() => {
        this.user.devices[deviceIndex].advertisementData = null;
      });
        console.warn('advertisementData reset', this.user.devices[deviceIndex]);
    }
  }

  async initPreviousTs() {
    try {
      console.log('initPreviousTs');

      const previousTs = await this.nativeStorage.getItem('previousTs').catch(err => console.log('1 err', err));
      if (previousTs) {
        this.previousTs = previousTs.previousTs;
        console.warn('1 this.previousTs',this.previousTs);

      } else {
        await this.nativeStorage.setItem('previousTs', {previousTs: Date.now()});
        const data = await this.nativeStorage.getItem('previousTs');
        if (data) {
          this.previousTs = data.previousTs;
          console.warn('2 this.previousTs', this.previousTs);
        }
      }
    } catch (error) {
      throw error;
    }
  }

  async initPreviousCumulativeMeasurement() {
    try {
      console.log('initPreviousCumulativeMeasurement');

      const previousCumulativeMeasurement = await this.nativeStorage.getItem('previousCumulativeMeasurement').catch(err => console.log('1 err', err));
      if (previousCumulativeMeasurement) {
        this.previousCumulativeMeasurement = previousCumulativeMeasurement.previousCumulativeMeasurement;
        console.warn('1 this.previousCumulativeMeasurement',this.previousCumulativeMeasurement);
      } else {
        const firstMeasurement: Measurement = {
          runtime: 0,
          water: 0,
          energy: 0,
          bootCount: 0,
          deviceId: 0,
          startTs: new Date().getTime(),
          endTs: new Date().getTime(),
          id: 0
        };
        const stringified = JSON.stringify(firstMeasurement)
        await this.nativeStorage.setItem('previousCumulativeMeasurement', {previousCumulativeMeasurement: stringified});
        const data = await this.nativeStorage.getItem('previousCumulativeMeasurement');
        if (data) {
          this.previousCumulativeMeasurement = data.previousCumulativeMeasurement;
          console.warn('2 this.previousCumulativeMeasurement', this.previousCumulativeMeasurement);
        }
      }
    } catch (error) {
      throw error;
    }
  }

  async initPreviousFromToSelection() {
    try {
      console.log('initPreviousFromToSelection');

      const previousFromToSelection = await this.nativeStorage.getItem('previousFromToSelection').catch(err => console.log('1 err', err));
      if (previousFromToSelection && previousFromToSelection.from && previousFromToSelection.to) {
        this.previousFromToSelection = previousFromToSelection;
        console.warn('1 this.previousFromToSelection', this.previousFromToSelection);
        return this.previousFromToSelection;
      } else {
        await this.nativeStorage.setItem('previousFromToSelection', {from: Date.now() - (1000 * 60 * 60 * 24 * 30), to: Date.now()});
        const data = await this.nativeStorage.getItem('previousFromToSelection');
        if (data) {
          this.previousFromToSelection = data;
          console.warn('2 this.previousFromToSelection',this.previousFromToSelection);
          return this.previousFromToSelection;
        }
      }
    } catch (error) {
      throw error;
    }
  }
}
