import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
// import { AuthGuard } from './auth.guard';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full', /* canActivate: [AuthGuard] */ },
  { path: 'home', loadChildren: './home/home.module#HomePageModule', /* canActivate: [AuthGuard] */ },
  { path: 'details', loadChildren: './details/details.module#DetailsPageModule', /* canActivate: [AuthGuard] */ },
  { path: 'settings', loadChildren: './settings/settings.module#SettingsPageModule', /* canActivate: [AuthGuard] */ },
  // { path: 'login', loadChildren: './login/login.module#LoginPageModule', },
  // { path: 'signup', loadChildren: './signup/signup.module#SignupPageModule', },
  { path: 'settings/devices', loadChildren: './devices/devices.module#DevicesPageModule', /* canActivate: [AuthGuard] */  },
  { path: 'settings/add-device', loadChildren: './add-device/add-device.module#AddDevicePageModule', /* canActivate: [AuthGuard] */  },
  { path: 'settings/units', loadChildren: './units/units.module#UnitsPageModule', /* canActivate: [AuthGuard] */  },
  { path: 'settings/about', loadChildren: './about/about.module#AboutPageModule', /* canActivate: [AuthGuard] */  },
  { path: 'settings/consumption-ranges', loadChildren: './consumption-ranges/consumption-ranges.module#ConsumptionRangesPageModule', /* canActivate: [AuthGuard] */  },
  { path: 'help', loadChildren: './help/help.module#HelpPageModule' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
