export class Device {
    constructor(
      public id: number,
      public name: string,
      public advertisementData?: AdvertisementData,
      public cumulativeData?: CumulativeData
    ) { }
  }

  interface AdvertisementData {
    runtime: number;
    temp: number;
    water: number;
    id: number;
    energy: number;
    cost: number;
    gasAmount: number;
  }

  interface CumulativeData {
    runtime: number;
    water: number;
    energy: number;
    bootCount: number;
  }
