  async updateMeasurement(measurement: Measurement) {
    // console.log('updateMeasurement', measurement);

    try {
      // console.log('input measurement', measurement);

      const measurementRepo = this.connection.getRepository(MeasurementEntity);
      let measurementToUpdate = await measurementRepo.findOne({
        where: {
          deviceId: measurement.deviceId,
          endTs: measurement.endTs
        }
      });
      // summauslogiikka


      if (measurementToUpdate) {
        const sum: Measurement = {
          runtime: measurementToUpdate.runtime + measurement.runtime,
          water: measurementToUpdate.water + measurement.water,
          energy: measurementToUpdate.energy + measurement.energy,
          bootCount: measurementToUpdate.bootCount + measurement.bootCount,
          deviceId: measurementToUpdate.deviceId,
          startTs: measurementToUpdate.startTs,
          endTs: measurementToUpdate.endTs,
          id: measurementToUpdate.id
        };
        // await measurementRepo.save(measurementToUpdate);
        await measurementRepo.update(measurementToUpdate.id, sum);

        const savedMeasurement = await measurementRepo.findOne({
          where: {
            deviceId: measurement.deviceId,
            id: measurementToUpdate.id
          }
        });
        console.log('updateMeasurement savedMeasurement', savedMeasurement);
        return savedMeasurement;

      } else {
        console.warn('no measurement to update')
      }




    } catch (error) {
      throw error;
    }
  }

  async getMeasurements(deviceIds: number[], from: number, to: number) {
    try {
      // console.log('deviceIds', deviceIds, from, to);

      const startTs = getTime(startOfDay(from));
      const endTs = getTime(endOfDay(to));

      const measurementsRepo = this.connection.getRepository(MeasurementEntity);
      let allMeasurements: Measurement[] = [];

      for (const id of deviceIds) {
        let measurements = await measurementsRepo.find({
          where: {
            deviceId: id,
            startTs: MoreThanOrEqual(startTs),
            endTs: LessThanOrEqual(endTs),
            order: {
              startTs: 'DESC'
            }
          }
        });
        allMeasurements = allMeasurements.length > 0 ? [...allMeasurements, ...measurements] : [...measurements];
      }
      const sorted = _.orderBy(allMeasurements, ['endTs', ['desc']])
      return sorted;
    } catch (error) {
      throw error;
    }
  }

